* {
  margin: 0;
  padding: 0;
  box-sizing: border-box !important;
}
* h1,
* h2,
* h3,
* h4,
* h5,
* h6 {
  font-size: 16px;
  font-weight: normal;
}
* a {
  transition: 0.3s all ease-in-out;
}
* a,
* a:hover {
  color: #f45c78;
  text-decoration: none;
}
* a:hover {
  color: #303860;
}
* ul {
  list-style: none !important;
}
* img {
  width: 100%;
  height: 100%;
}
* svg,
* i {
  font-size: 24px !important;
}
* button,
* input,
* textarea,
* select {
  border: none;
  font-family: Newsreader;
}
* button:focus, * button:hover,
* input:focus,
* input:hover,
* textarea:focus,
* textarea:hover,
* select:focus,
* select:hover {
  outline: none;
}
* .cta {
  width: 100px;
  height: 35px;
}
* .cta button {
  width: 100%;
  height: 100%;
  font-weight: bold;
  font-size: 14px;
  font-family: Ubuntu;
  color: #fafafa;
  background-color: #f45c78;
  border-radius: 5px;
  padding: 5px;
  transition: 0.3s all ease-in-out;
}
* .cta button:hover {
  background-color: #fafafa;
  color: #303860 !important;
  box-shadow: -2.5px 0 5px rgba(0, 0, 0, 0.1), 0 -2.5px 10px rgba(0, 0, 0, 0.1);
}
* .cta-line {
  height: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
* .cta-line span {
  width: 50px;
  height: 100%;
  background-color: #f45c78;
  align-self: center;
}
* .cta-line-left {
  height: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
* .cta-line-left span {
  width: 50px;
  height: 100%;
  background-color: #f45c78;
  align-self: flex-start;
}
* .cta-line-right {
  height: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
* .cta-line-right span {
  width: 50px;
  height: 100%;
  background-color: #f45c78;
  align-self: flex-end;
}
* .hidden {
  display: none !important;
}
* .form-control {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
* .form-control input.form-control-input,
* .form-control textarea.form-control-input,
* .form-control select.form-control-input,
* .form-control button.form-control-input {
  width: 100%;
  height: 100%;
  padding: 10px;
  padding-right: 0;
  border-radius: 5px;
}
* .form-control input.form-control-input:focus,
* .form-control textarea.form-control-input:focus,
* .form-control select.form-control-input:focus,
* .form-control button.form-control-input:focus {
  border: 1px solid #f45c78;
  box-shadow: -2.5px 0 5px rgba(0, 0, 0, 0.1), 0 -2.5px 10px rgba(0, 0, 0, 0.1);
}
* .form-control-cta {
  display: flex;
  flex-direction: row;
  align-items: center;
  align-content: center;
  justify-content: flex-start;
}
* .sticky {
  position: -webkit-sticky !important;
  position: sticky !important;
  top: 0 !important;
}
* .outline,
* .outline * {
  outline: 1px dashed red;
}

.carousel {
  width: 100vw;
  height: 18.75rem;
  position: relative;
  background-color: darkcyan;
}
.carousel > div,
.carousel button {
  /* overflow-x: hidden;overflow-y: hidden; */
  background-color: transparent;
}
.carousel-indicators {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: max-content;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  column-gap: 0.3125rem;
  padding: 0.625rem;
}
.carousel-indicators button {
  width: 0.9375rem;
  height: 0.15625rem;
  background: #9a9a9a;
  transition: 0.3s all ease-in-out;
}
.carousel-indicators button.active {
  background: #fafafa;
}
.carousel-inner {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(3, 100%);
  overflow-x: hidden;
}
.carousel-inner .carousel-item {
  padding: 1.875rem;
}
.carousel-control-prev {
  left: 0;
}
.carousel-control-next {
  right: 0;
}
.carousel-control-prev, .carousel-control-next {
  position: absolute;
  top: 0;
  width: max-content;
  height: 100%;
  color: #fafafa;
  font-weight: 900;
  font-size: 1.5rem;
  padding: 0 0.625rem;
}